.sidebar {
    display: none;
}

.sidebar.open {
    display: block;
    position: absolute;
    width: 317px;
    height: 100%;
    background-color: #fff;
    z-index: 1;
    left: 0px;
    top: 0px;
    box-shadow: 0px 10px 40px rgba(29, 33, 38, 0.13), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 0px;
}

.background {}

.nav {
    /* display: flex;
    flex-direction: column; */
    margin-left: 70px;
    margin-right: auto;
}

.nav a {
    display: block;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    color: black;
    text-decoration-line: none;
    margin-bottom: 45px;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
    text-decoration-line: underline;
    color: #0059B2;
}

.close {
    display: block;
    margin: 16px 24px 16px auto;
    border: none;
    cursor: pointer;
    background-color: #fff;
    /* padding: 16px 24px; */
}

